.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.about__me {
  width: 100%;
 height: 63%;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
  
  
}

.about__me-image {
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  transition-duration: 1s;
}

.about__me-image:hover {
  transform: rotate(0);
  /* transition: var(--transition);  */
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
   
    
    
}


.about__card{
    background-color: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    opacity: 0.75;
    transition-duration: 0.8s;
}

.about__card:hover {
    background: transparent;
    border-color: var(--color-primary);
}

.about__icon{
    color: var(--color-primary);
    font-size: 2rem;
    margin-bottom: 1rem;

}

.about__card h5{
    font-size: 0.95rem;
}

.about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
}

.about__content p{
    margin: 2rem 0 2.6rem;
    color: var(--color-light);
}

.about__content a{
    padding: 10px 10px;
}



/* =============================Media Queries ============================ */
 @media screen and (max-width:1024px) {
    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__me p{
        margin: 1rem 0 1.5rem;
    }

}

@media screen and (max-width:600px) {
  .about__me {
    width: 65%;
    margin: 0 auto 3rem;
  }

  .about__cards {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
    margin-top: 100px;
}

.about__content{
    text-align: center;

}

.about__content p{
    margin: 1.5rem 0;
} 


}






